import React from "react";
import axios from "axios";
import { useState, useEffect } from "react";
import RowFruits from "./RowFruits";
import Transaction from "./Transaction";

import Box from "@mui/material/Box";
import Alert from "@mui/material/Alert";

import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";

import FormControl from "@mui/material/FormControl";
import Button from "@mui/material/Button";
import InputLabel from "@mui/material/InputLabel";

import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";

import DeleteIcon from "@mui/icons-material/Delete";
import IconButton from "@mui/material/IconButton";

import CircularProgress from "@mui/material/CircularProgress";

import Modal from "@mui/material/Modal";

const Shop = ({ currentUser, fruits, setFruits }) => {
  const [sessionFruits, setSessionFruits] = useState(fruits);

  const url = "https://react-mongo-lloyd.herokuapp.com/api/";

  const handleConfirm = () => {
    showLoading(true);
    let total = 0;
    for (var key in cartFruits) {
      total += cartFruits[key].totalPrice;
    }
    const tempTransaction = {
      name: currentUser.name,
      userId: currentUser._id,
      items: cartFruits,
      total: total,
    };
    // const result = await axios.post(`${url}transactions`, {
    //   name: currentUser.name,
    //   userId: currentUser._id,
    //   items: cartFruits,
    //   total: total,
    // });
    // const id = result.data.data.insertedId;
    // const transaction = await axios.get(`${url}transactions/${id}`);
    // setTransactionItems(transaction.data);

    setTransactionItems(tempTransaction);
    console.log(tempTransaction);

    showTransaction(true);
    showLoading(false);
  };

  const handleBuy = async () => {
    const result = await axios.post(`${url}transactions`, {
      name: transactionItems.name,
      userId: transactionItems.userId,
      items: transactionItems.items,
      total: transactionItems.total,
    });
    const id = result.data.data.insertedId;
    const transaction = await axios.get(`${url}transactions/${id}`);
    console.log(transaction.data.datax);
  };

  const [selectedFruit, setSelectedFruit] = useState("default");
  const [cartFruits, setCartFruits] = useState([]);
  const [alertState, setAlertState] = useState(false);
  const [transactionState, showTransaction] = useState(false);
  const [transactionItems, setTransactionItems] = useState([]);
  const [loading, showLoading] = useState(false);

  const handleRemove = (fruitRemove) => {
    let backFruit = sessionFruits.filter((fruit) => fruit.item === fruitRemove);
    let filteredFruit = cartFruits.filter(
      (fruit) => fruit.item !== fruitRemove
    );
    setCartFruits(filteredFruit);
    const itemObject = backFruit[0];
    setFruits([...fruits, itemObject]);
  };
  const handleRemoveAll = () => {
    setCartFruits([]);
    setFruits(sessionFruits);
    console.log(fruits);
  };
  const handleAddFruit = (select) => {
    const item = fruits.find((fruit) => fruit.item === select);
    const filterItem = fruits.filter((fruit) => fruit.item !== select);
    console.log(filterItem);
    setFruits(filterItem);
    setSelectedFruit("default");

    // //check if dupe
    // // for each fruit in cart

    // for (var key in cartFruits) {
    //   if (select === cartFruits[key].item) {
    //     setAlertState(true);
    //     return; //reject action here
    //   }
    // }
    // setAlertState(false);

    const itemObject = {
      item: select,
      price: item.price,
      count: 1,
      totalPrice: item.price,
    };

    //only works for adding new items
    setCartFruits([...cartFruits, itemObject]);
  };

  const handleCount = (fruitItem, fruitPrice, count) => {
    const mappedFruits = cartFruits.map((fruit) =>
      fruit.item === fruitItem
        ? { ...fruit, count: count, totalPrice: fruitPrice * count }
        : fruit
    );
    setCartFruits(mappedFruits);
  };

  return (
    <>
      {/* TO DO:<Modal open={true} onClose={handleCloseModal}></Modal> */}
      {alertState ? (
        <Alert
          severity="error"
          className="mb-3"
          onClose={() => {
            setAlertState(false);
          }}
        >
          Fruit already added
        </Alert>
      ) : (
        ""
      )}
      <Box className="">
        <h2>Shop</h2>
        <Box className="row justify-content-center">
          <Box className="">Hello {currentUser.name}</Box>
        </Box>
        <Box className="row justify-content-center w-50 m-auto">
          <FormControl className="col-9" size="small">
            <InputLabel id="select-label-fruits">Fruits</InputLabel>
            {/* checks if fruits are loaded from database */}
            {/* fruits.length > 1 */}

            <Select
              disabled={fruits.length === 0 || transactionState ? true : false}
              labelId="select-label-fruits"
              id="select-fruits"
              value={selectedFruit}
              label="Fruits"
              onChange={(e) => {
                setSelectedFruit(e.target.value);
              }}
            >
              <MenuItem disabled value={"default"}>
                Select Fruit
              </MenuItem>
              {fruits.map((fruit, index) => (
                <MenuItem key={index} value={fruit.item}>
                  {fruit.item}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <Button
            disabled={
              selectedFruit === "default" || transactionState ? true : false
            }
            variant="contained"
            className="col-1"
            onClick={() => {
              handleAddFruit(selectedFruit);
            }}
          >
            Add
          </Button>
        </Box>
      </Box>
      <Box className="row ">
        <List>
          <ListItem
            divider={true}
            secondaryAction={
              cartFruits.length > 0 && (
                <IconButton
                  disabled={transactionState}
                  onClick={() => {
                    handleRemoveAll();
                  }}
                >
                  <DeleteIcon />
                </IconButton>
              )
            }
          >
            <Box className="col-3 text-center">Item</Box>
            <Box className="col-3 text-center">Price</Box>
            <Box className="col-3 text-center">Kilo</Box>

            <Box className="col-3 text-center">Total</Box>
          </ListItem>
          {cartFruits.length > 0 ? (
            cartFruits.map((fruit, index) => (
              <RowFruits
                transactionState={transactionState}
                fruitItem={fruit.item}
                fruitPrice={fruit.price}
                fruitCount={fruit.count}
                fruitTotal={fruit.totalPrice}
                key={index}
                handleRemove={handleRemove}
                handleCount={handleCount}
              />
            ))
          ) : (
            <Box className=" text-center">No Fruits</Box>
          )}
        </List>
      </Box>
      <Box className="justify-content-end d-flex">
        <Button
          className=""
          disabled={transactionState || cartFruits.length === 0 ? true : false}
          variant="contained"
          onClick={() => {
            handleConfirm();
          }}
        >
          Confirm
        </Button>
      </Box>
      <Box>
        {transactionState ? (
          <Transaction
            handleBuy={handleBuy}
            data={transactionItems}
            showTransaction={showTransaction}
          />
        ) : (
          // <Transaction data={transactionItems.data} />

          loading && (
            <div className="text-center">
              <CircularProgress />
            </div>
          )
        )}
      </Box>
    </>
  );
};

export default Shop;
