import React from "react";

import { useState } from "react";
import Counter from "./Counter";

import ListItem from "@mui/material/ListItem";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from "@mui/icons-material/Delete";
import Box from "@mui/material/Box";

const RowFruits = ({
  fruitItem,
  fruitPrice,
  handleRemove,
  fruitCount,
  handleCount,
  fruitTotal,
  transactionState,
}) => {
  const handleChangeCount = (number) => {
    setCount(number);
    handleCount(fruitItem, fruitPrice, number);
  };
  const [count, setCount] = useState(fruitCount);

  return (
    <ListItem
      divider={true}
      secondaryAction={
        <IconButton
          disabled={transactionState}
          onClick={() => {
            handleRemove(fruitItem);
          }}
        >
          <DeleteIcon />
        </IconButton>
      }
    >
      <Box className="col-3 text-center">{fruitItem}</Box>
      <Box className="col-3 text-center">{fruitPrice}</Box>
      <Counter
        transactionState={transactionState}
        count={count}
        handleChangeCount={handleChangeCount}
        fruitItem={fruitItem}
        fruitCount={fruitCount}
      />
      <Box className="col-3 text-center">{fruitPrice * count}</Box>
    </ListItem>
  );
};

export default RowFruits;
