import axios from "axios";
import Login from "./components/Login";
import Register from "./components/Register";
import Shop from "./components/Shop";
import "./App.css";
import { useState, useEffect } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import { Navigate } from "react-router-dom";

import CircularProgress from "@mui/material/CircularProgress";

import Box from "@mui/material/Box";
import Alert from "@mui/material/Alert";

function App() {
  const url = "https://react-mongo-lloyd.herokuapp.com/api/";

  const [fruits, setFruits] = useState([]);
  const [loading, setLoading] = useState(false);

  const [regLoading, setRegLoading] = useState(false);
  const [regSuccess, setRegSuccess] = useState(false);
  const [justRegistered, setJustRegistered] = useState(false);

  const [invalidRegister, setInvalidRegister] = useState(false);
  const [passwordMismatch, setPasswordMismatch] = useState(false);
  const [emailExists, setEmailExists] = useState(false);
  const [fieldsEmpty, setFieldsEmpty] = useState(false);

  const [accountNotExists, setAccountNotExists] = useState(false);
  const [loginFail, setLoginFail] = useState(false);
  const [loginLoading, setLoginLoading] = useState(false);
  const [invalidEmail, setInvalidEmail] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const [currentUser, setCurrentUser] = useState("");

  const getAllFruits = async () => {
    // Till the data is fetched using API
    // the Loading page will show.
    setLoading(true);

    // Await will make it wait until that
    // promise settles and return its result

    const response = await axios.get(`${url}items`);

    setFruits(response.data.data);
    setLoading(false);
  };

  //runs on startup
  useEffect(() => {
    getAllFruits();
  }, []);

  const handleRegister = async (email, pass, name, passConfirmInput, e) => {
    e.preventDefault();

    setRegLoading(false);
    setRegSuccess(false);
    setInvalidRegister(false);
    setFieldsEmpty(false);
    setPasswordMismatch(false);
    setEmailExists(false);

    //checks if fields are empty
    if (email === "" || pass === "" || passConfirmInput === "" || name === "") {
      console.log("empty");
      setFieldsEmpty(true);
      return;
    }

    if (!validateEmail(email)) {
      setInvalidRegister(true);
      return;
    }

    if (pass !== passConfirmInput) {
      setPasswordMismatch(true);
      return;
    }
    setRegLoading(true);

    const existingEmail = await axios.post(`${url}users/validate`, {
      email: email,
    });

    if (existingEmail.data["data"] !== null) {
      setEmailExists(true);
      setRegLoading(false);

      return;
    }

    const response = await axios.post(`${url}users`, {
      name: name,
      pass: pass,
      email: email,
    });

    if (response.status === 200) {
      setRegLoading(false);
      setRegSuccess(true);
      setJustRegistered(true);
      setJustRegistered(false);
    }
  }; // end of handleRegister

  const handleLogin = async (email, pass, e) => {
    e.preventDefault();
    setRegSuccess(false);

    setFieldsEmpty(false);
    setAccountNotExists(false);

    setLoginFail(false);
    setLoginLoading(false);
    setInvalidEmail(false);

    if (email === "" || pass === "") {
      setFieldsEmpty(true);
      return;
    }

    if (!validateEmail(email)) {
      setInvalidEmail(true);
      return;
    }

    setLoginLoading(true);

    const existingEmail = await axios.post(`${url}users/validate`, {
      email: email,
    });

    if (existingEmail.data["data"] === null) {
      setAccountNotExists(true);
      setLoginLoading(false);
      return;
    }

    const response = await axios.post(`${url}users/login`, {
      email: email,
      pass: pass,
    });

    if (response.data["data"] === null) {
      setLoginLoading(false);
      setLoginFail(true);
      return;
    }
    setCurrentUser(response.data["data"]);
    setIsLoggedIn(true);
  }; //end of handleLogin

  const validateEmail = (email) => {
    let re =
      /[a-zA-Z0-9!@#$%&'*+'-=]+@[a-zA-Z0-9!@#$%&'*+'-=]+\.[a-zA-Z0-9!@#$%&'*+'-=]+/;
    return re.test(email);
  };
  return (
    <Router>
      <Box className="container-sm w-50 my-2 p-3 dark-border text">
        {fieldsEmpty && (
          <Alert
            severity="error"
            onClose={() => {
              setFieldsEmpty(false);
            }}
          >
            Please fill out all fields{" "}
          </Alert>
        )}
        {regSuccess && (
          <Alert
            severity="success"
            onClose={() => {
              setRegSuccess(false);
            }}
          >
            Registration successful
          </Alert>
        )}
        {loading ? (
          <div className="text-center">
            <CircularProgress />
          </div>
        ) : (
          <Routes>
            {isLoggedIn ? (
              <Route path="/" element={<Navigate to="/shop" />} />
            ) : (
              <Route
                path="/"
                element={
                  <Login
                    handleLogin={handleLogin}
                    loginLoading={loginLoading}
                    accountNotExists={accountNotExists}
                    setAccountNotExists={setAccountNotExists}
                    loginFail={loginFail}
                    setLoginFail={setLoginFail}
                    invalidEmail={invalidEmail}
                    setInvalidEmail={setInvalidEmail}
                  />
                }
              />
            )}
            {justRegistered ? (
              <Route path="/register" element={<Navigate to="/" />} />
            ) : (
              <Route
                path="/register"
                element={
                  <Register
                    handleRegister={handleRegister}
                    regLoading={regLoading}
                    invalidRegister={invalidRegister}
                    passwordMismatch={passwordMismatch}
                    setPasswordMismatch={setPasswordMismatch}
                    emailExists={emailExists}
                    setEmailExists={setEmailExists}
                    fieldsEmpty={fieldsEmpty}
                    setFieldsEmpty={setFieldsEmpty}
                  />
                }
              />
            )}
            {isLoggedIn ? (
              <Route
                path="/shop"
                element={
                  <Shop
                    currentUser={currentUser}
                    fruits={fruits}
                    setFruits={setFruits}
                  />
                }
              />
            ) : (
              <Route path="/shop" element={<Navigate to="/" />} />
            )}
          </Routes>
        )}
      </Box>
    </Router>
  );
}

export default App;
