import { useState } from "react";
import { Link } from "react-router-dom";

import Alert from "@mui/material/Alert";

import Input from "@mui/material/Input";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

import Button from "@mui/material/Button";

const Register = ({
  handleRegister,
  regLoading,
  invalidRegister,
  handleInvalidRegisterClose,
  passwordMismatch,
  setPasswordMismatch,
  emailExists,
  setEmailExists,
}) => {
  const [emailInput, setEmailInput] = useState("");
  const [passInput, setPassInput] = useState("");
  const [passConfirmInput, setPassConfirmInput] = useState("");

  const [nameInput, setNameInput] = useState("");
  const [showPass, setShowPass] = useState(false);
  return (
    <div className="">
      {passwordMismatch && (
        <Alert
          severity="error"
          className="my-3"
          onClose={() => {
            setPasswordMismatch(false);
          }}
        >
          Passwords do not match
        </Alert>
      )}
      {emailExists && (
        <Alert
          severity="error"
          onClose={() => {
            setEmailExists(false);
          }}
        >
          Email already exists{" "}
        </Alert>
      )}
      {regLoading && <Alert severity="info"> Registering... </Alert>}
      {invalidRegister && (
        <Alert
          severity="error"
          onClose={() => {
            handleInvalidRegisterClose();
          }}
        >
          Invalid Email Address
        </Alert>
      )}
      <h2>Register</h2>
      <form>
        <label className="h5 d-block">Name</label>
        {/* <InputLabel>Name</InputLabel> */}
        <Input
          fullWidth={true}
          placeholder="Name"
          value={nameInput}
          onChange={(e) => {
            setNameInput(e.target.value);
          }}
        />
        <label className="h5 mt-2">Email</label>
        <Input
          fullWidth={true}
          type="email"
          placeholder="Email"
          value={emailInput}
          onChange={(e) => {
            setEmailInput(e.target.value);
          }}
        />
        <label className="h5 mt-2">Password</label>
        <Input
          fullWidth={true}
          type={showPass ? "text" : "password"}
          placeholder="Password"
          value={passInput}
          onChange={(e) => {
            setPassInput(e.target.value);
          }}
          endAdornment={
            <InputAdornment
              position="end"
              onClick={() => {
                setShowPass(!showPass);
              }}
            >
              <IconButton>
                {showPass ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
        />

        <label className="h5 mt-2">Confirm Password</label>
        <Input
          fullWidth={true}
          type={showPass ? "text" : "password"}
          placeholder="Password"
          value={passConfirmInput}
          onChange={(e) => {
            setPassConfirmInput(e.target.value);
          }}
          endAdornment={
            <InputAdornment
              position="end"
              onClick={() => {
                setShowPass(!showPass);
              }}
            >
              <IconButton>
                {showPass ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
        />
        <Button
          className="my-3"
          type="submit"
          fullWidth={true}
          // sx={{ backgroundColor: "#3f2d88" }}
          variant="contained"
          onClick={(e) => {
            handleRegister(
              emailInput,
              passInput,
              nameInput,
              passConfirmInput,
              e
            );
          }}
        >
          Register
        </Button>
      </form>

      <Link to={{ pathname: "/" }}>Have an account? Login here</Link>
    </div>
  );
};

export default Register;
