import React from "react";
import ListItem from "@mui/material/ListItem";
import Box from "@mui/material/Box";

const TransactionRow = ({ item }) => {
  return (
    //   TODO: use tables instead
    <>
      <ListItem divider={true}>
        <Box className="col-3 text-center">{item.item}</Box>
        <Box className="col-3 text-center">{item.price}</Box>
        <Box className="col-3 text-center">{item.count} kg</Box>
        <Box className="col-3 text-center">{item.totalPrice}</Box>
      </ListItem>
    </>
  );
};

export default TransactionRow;
