import { useState } from "react";
import { Link } from "react-router-dom";

import Alert from "@mui/material/Alert";

import Input from "@mui/material/Input";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

import Button from "@mui/material/Button";
import Box from "@mui/material/Box";

const Login = ({
  handleLogin,
  loginLoading,
  loginFail,
  invalidEmail,
  accountNotExists,
  setAccountNotExists,
  setLoginFail,
  setInvalidEmail,
}) => {
  const [emailInputLogin, setEmailInputLogin] = useState("");
  const [passInputLogin, setPassInputLogin] = useState("");
  const [showPass, setShowPass] = useState(false);
  return (
    <Box className="">
      {accountNotExists && (
        <Alert
          severity="error"
          onClose={() => {
            setAccountNotExists(false);
          }}
        >
          Account does not exist
        </Alert>
      )}
      {loginFail && (
        <Alert
          severity="error"
          onClose={() => {
            setLoginFail(false);
          }}
        >
          Login failed
        </Alert>
      )}
      {invalidEmail && (
        <Alert
          severity="error"
          onClose={() => {
            setInvalidEmail(false);
          }}
        >
          Invalid Email Address
        </Alert>
      )}
      {loginLoading && <Alert severity="info">Logging in...</Alert>}
      <form>
        <h2>Login</h2>
        <label className="h5">Email</label>
        <Input
          fullWidth={true}
          placeholder="Email"
          value={emailInputLogin}
          onChange={(e) => {
            setEmailInputLogin(e.target.value);
          }}
        />

        <label className="h5 mt-2">Password</label>
        <Input
          fullWidth={true}
          type={showPass ? "text" : "password"}
          placeholder="Password"
          value={passInputLogin}
          onChange={(e) => {
            setPassInputLogin(e.target.value);
          }}
          endAdornment={
            <InputAdornment
              position="end"
              onClick={() => {
                setShowPass(!showPass);
              }}
            >
              <IconButton>
                {showPass ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
        />
        <Button
          type="submit"
          fullWidth={true}
          className="my-3"
          variant="contained"
          onClick={(e) => {
            handleLogin(emailInputLogin, passInputLogin, e);
          }}
        >
          Login
        </Button>
        <Link to={{ pathname: "./register" }}>
          Don't have an account? Register here
        </Link>
      </form>
    </Box>
  );
};

export default Login;
