import React from "react";

import TransactionRow from "./TransactionRow";

import Box from "@mui/material/Box";

import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Button from "@mui/material/Button";

const Transaction = ({ data, showTransaction, handleBuy }) => {
  return (
    <>
      <h2>Transaction</h2>
      User: {data.name}
      {/* {data.userId} */}
      <List>
        <ListItem>
          <Box className="col-3 text-center">Item</Box>
          <Box className="col-3 text-center">Price</Box>
          <Box className="col-3 text-center">Kilo</Box>
          <Box className="col-3 text-center">Total Price</Box>
        </ListItem>
        {data.items.length > 0
          ? data.items.map((item, index) => (
              <TransactionRow key={index} item={item} />
            ))
          : ""}
        <ListItem>
          <Box className="col-3 text-center">Final Bill</Box>
          <Box className="col-3 text-center"></Box>
          <Box className="col-3 text-center"></Box>
          <Box className="col-3 text-center">
            {/* <strong>{data.finalBill}</strong> */}
            <strong>{data.total}</strong>
          </Box>
        </ListItem>
      </List>
      <Box className="d-flex justify-content-between">
        <Button
          variant="contained"
          onClick={() => {
            showTransaction(false);
          }}
        >
          Edit
        </Button>
        <Button
          variant="contained"
          onClick={() => {
            handleBuy();
          }}
        >
          Buy
        </Button>
      </Box>
    </>
  );
};

export default Transaction;
